.ticket-nav-bar {
    display: flex;
    justify-content: space-around;
    margin: 1rem 0;
    .add-ticket-link {
        position: fixed;
        bottom: 2%;
        right: 2%;
        background-color: $main-color;
        color: $color3;
        padding: 15px;
        border-radius: 50%;
        box-shadow: 0 3px 6px rgba(0,0,0,0.19), 0 3px 3px rgba(0,0,0,0.23);

    }
}